import { Component, Input, ViewEncapsulation, OnInit } from '@angular/core';
import { JSONBuilder } from '../../../../services/JSONBuilder.service';
declare var jQuery: any;
@Component({
  selector: 'selectize',
  template: `
      <div class="input-group">
            <span class="no-padding option-label">
               <span class="astrik-label" *ngIf="field.validations.required.status">*</span>
                <span class="astrik-label not-man" *ngIf="!field.validations.required.status">*</span>
            </span>
          <div class="col-md-10 col-xs-10 no-padding">
            <select class="select-default"  [(ngModel)]="field.type" (change)="changed($event,field)">
                <option value="firstName">Name</option>
                <option value="email">Email</option>
                <option value="tel">Phone Number</option>
                <option value="lastName">Others</option>
            </select> 
          </div>
          <div class="col-md-12 " style="padding-right: 0px;" *ngIf="_JSONBuilder.getSelectedPage().type ==='Questionnaire'">
            <label class="check-value">TITLE</label>
            <input type="text" class="form-control form-text value-text "  [(ngModel)] = "field.name">
          </div>
          <div class="col-md-12 " style="padding-right: 0px;">
            <label class="check-value">PLACEHOLDER</label>
            <input type="text" class="form-control form-text value-text "  [(ngModel)] = "field.placeholder">
          </div>
        </div>
    `,
  encapsulation: ViewEncapsulation.None
})

export class Selectize implements OnInit {
  @Input() field: any;
  @Input() index: any;
  constructor(private _JSONBuilder: JSONBuilder) {
  }

  ngOnInit() {
  }

  changed($event: any, field: any) {
    let type = $event.target.value;
    if (type === 'firstName') {
      field.name = 'Name';
      field.placeholder = 'John Doe';
    } else if (type === 'email') {
      field.name = 'Email';
      field.placeholder = 'John@outgrow.co';
    } else if (type === 'tel') {
      field.name = 'Phone Number';
      field.placeholder = '312-917-8106';
    } else if (type === 'lastName') {
      field.name = 'Others';
      field.placeholder = 'Others';
    }
  }
}



